.page-title {
  margin-bottom: 35px;
}

.breadcrumbs {
  font-size: 14px;
}
.breadcrumbs a {
  color: #868686;
}

.archive-wrapper {
  padding-top: 35px;
}

.m-75 {
  margin: 75px auto;
}

.pagination {
  display: flex;
  gap: 10px;
  padding: 10px;
  border-radius: 50px;
  background: #fff;
  max-width: fit-content;
}
.pagination .page-link {
  min-width: 40px;
  min-height: 40px;
  border-radius: 200px !important;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  font-size: 18px;
}
.pagination .page-link:not(.current) {
  background: #F5F5F5;
}

.pagination-wrapper ul {
  display: flex;
  gap: 10px;
  padding: 10px;
  border-radius: 50px;
  background: #fff;
  max-width: fit-content;
  list-style: none;
}
.pagination-wrapper ul a, .pagination-wrapper ul span {
  min-width: 40px;
  min-height: 40px;
  border-radius: 200px !important;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  font-size: 18px;
}
.pagination-wrapper ul a.current, .pagination-wrapper ul span.current {
  background: #31312F;
  color: #fff;
}
.pagination-wrapper ul a:not(.current), .pagination-wrapper ul span:not(.current) {
  background: #F5F5F5;
}

.blog .content-wrapper {
  padding: 15px 30px;
}
.blog .image-wrapper,
.blog .content-wrapper {
  flex: 0 0 50%;
  max-width: 50%;
}
.blog .single-item {
  padding: 15px;
  border-radius: 20px;
}
.blog .single-item img {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.blog .title {
  margin-bottom: 15px;
}
.blog .content {
  color: #868686;
}

.doctors .image-wrapper {
  aspect-ratio: var(--aspect-ratio, 275/300);
}
.doctors .single-item {
  padding: 15px;
  border-radius: 20px;
}
.doctors img {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.doctors .content-wrapper {
  padding: 15px;
}
.doctors .content-wrapper .title {
  margin-top: 15px;
  margin-bottom: 12px;
}
.doctors .experience {
  font-size: 16px;
  background: #fff;
  padding: 5px 15px;
  border-radius: 100px;
  left: 10px;
  bottom: 10px;
  z-index: 9;
}
.doctors .qualification {
  margin-top: 15px;
  color: #868686;
  font-size: 16px;
  margin-bottom: 12px;
}
.doctors .specialties {
  margin-bottom: 16px;
  font-size: 16px;
}

.loading {
  text-align: center;
  padding: 20px;
  font-style: italic;
  color: #666;
}

#specialty-filter {
  width: 413px;
  padding-left: 60px;
  height: 60px;
  color: #868686;
}
@media (max-width: 767px) {
  #specialty-filter {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .specialty {
    width: 100%;
  }
}

.svg_select {
  left: 10px;
  bottom: 10px;
  z-index: 10;
}

