  border: 1px solid #D8D8D8;
}
.single-doctors .accordion .accordion-button:focus {
  box-shadow: none;
}
.single-doctors .accordion .accordion-button::after {
  width: 40px;
  height: 40px;
  background-color: var(--bs-secondary);
  background-size: 10px 5px;
  background-position: center;
  border-radius: 50%;
}
.single-doctors .consultation {
  gap: 15px;
